import React from 'react';
import { FiChevronRight } from 'react-icons/fi';
import { BiRupee } from 'react-icons/bi';
import { Link, useRouteMatch } from 'react-router-dom';
import { ArrowLeftIcon } from '@heroicons/react/outline';
import { DaughterIcon, FemaleIcon, MaleIcon, ParentsIcon, RingIcon, SonIcon } from './AppIcons';

export const AppRow = ({ children, className = "" }) => {
  return <div className={["flex flex-wrap flex-col md:flex-row space-x-0 md:space-x-5 space-y-5 md:space-y-0", className].join(" ")}>
    {children}
  </div>
}

export const AppHeading = ({ children, color = "black", className = "", size = "2xl", font = "medium" }) => {
  return <h3 className={[`text-${size} font-${font} text-${color}`, className].join(" ")}>{children}</h3>
}

export const AppCard = ({ children, title = "", className = "", attrs = {} }) => {
  return <div className={[`rounded-xl bg-primary-50 border border-primary p-4`, className].join(" ")} {...attrs}>
    {title ? <h5 className={`text-base mb-2`}>{title}</h5> : false}
    {children}
  </div>
}

export const AppSidebarMenu = ({ children }) => {
  return <div className="flex md:flex-col flex-row">
    {children}
  </div>
}

export const AmountText = ({ amount }) => {

  amount = new Intl.NumberFormat('en-IN', { maximumSignificantDigits: 3 }).format(amount)

  return <div className="flex items-center"><BiRupee size={17} /> {amount}</div>
}

export const AppSidebarMenuItem = ({ children, title = '', link = '/#' }) => {
  const active = useRouteMatch(link);

  return <Link to={link} className={[`flex items-center justify-between px-4 py-6 hover:text-primary-600 focus:outline-none md:border-b last:border-0`, active ? 'text-primary' : ''].join(' ')}>
    <div className="flex-1 flex whitespace-nowrap">
      <div className="inline-block mr-3">{children}</div>
      <p className="text-sm font-semibold">{title}</p>
    </div>
    <span className="md:inline-block hidden">
      <FiChevronRight size={20} />
    </span>
  </Link>
}

export const AppBackButton = ({ to = "/", children }) => {
  return <div className="px-6 text-primary mb-6">
    <Link to={to}>
      <div className="flex items-center">
        <ArrowLeftIcon width={17} className="mr-3" />
        <p>{children}</p>
      </div>
    </Link>
  </div>
}

export const AppStatusLabel = ({ status, active = "Active", inactive = "Declined", pending = "Pending", waiting = "Waiting" }) => {
  switch (status) {
    case 1:
      return <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
        {active}
      </span>
    case 2:
      return <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-red-100 text-red-600">
        {inactive}
      </span>
    case 3:
      return <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-orange-500 text-white">
        {waiting}
      </span>
    case 0:
    default:
      return <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-yellow-100 text-yellow-800">
        {pending}
      </span>
  }
}

export const AppLabel = ({ title, color = "primary" }) => {

  let text_color = "text-primary-800"
  let bg_color = "bg-primary-100"

  switch (color) {
    case "blue":
      text_color = "text-blue-800"
      bg_color = "bg-blue-100"
      break;
    case "indigo":
      text_color = "text-indigo-800"
      bg_color = "bg-indigo-100"
      break;
    case "purple":
      text_color = "text-purple-800"
      bg_color = "bg-purple-100"
      break;
    case "pink":
      text_color = "text-pink-800"
      bg_color = "bg-pink-100"
      break;
    case "orange":
      text_color = "text-orange-800"
      bg_color = "bg-orange-100"
      break;
    case "teal":
      text_color = "text-teal-800"
      bg_color = "bg-teal-100"
      break;
    default:
      text_color = `text-${color}-800`
      bg_color = `bg-${color}-100`
      break;
  }

  return <span className={`px-3 inline-flex text-xs leading-5 font-semibold rounded-full ${text_color} ${bg_color}`}>
    {title}
  </span>
}

export const MemberIcon = ({ member, size = 24 }) => {
  if (member.relation === 'self' && member.gender === 'm') return <MaleIcon size={size} />

  if (member.relation === 'self' && member.gender === 'f') return <FemaleIcon size={size} />

  if (member.relation === 'spouse') return <RingIcon size={size} />

  if (member.relation === 'daughter') return <DaughterIcon size={size} />

  if (member.relation === 'son') return <SonIcon size={size} />

  if (member.relation === 'parent' || member.relation === 'spouse_parent') return <ParentsIcon size={size} />

  return false
}