import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import { AmountText, AppBackButton, AppStatusLabel } from '../components/AppComponents';
import Button from '../components/Button';
import { ApiGet } from '../lib/AppHelper';
import { AppStore } from '../lib/AppStore';
import Main from './Main'

export class Subscriptions extends Component {

  MessageRef = React.createRef();

  state = {
    subscriptions: []
  }

  render() {
    const { subscriptions } = this.state;
    const { loading } = this.props;

    return (
      <Main MessageRef={this.MessageRef}>
        <AppBackButton to="/">Back to Dashboard</AppBackButton>
        <div className="w-full px-6 flex flex-col justify-between space-y-6">
          <div>
            {subscriptions.length === 0 ? <>
              {loading ? <p>loading...</p> : <>
                <div className="text-center">
                  <p className="text-xl">You do not have any subscriptions</p>
                  <Link to="/subscribe"><Button title="Add New Member" className="max-w-min mx-auto mt-4" /></Link>
                </div>
              </>}
            </> : <>
              <div className="flex flex-col">
                <div className="-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
                  <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                    <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                      <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-100">
                          <tr>
                            <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Customer
                            </th>
                            <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Product
                            </th>
                            <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Status
                            </th>
                            <th scope="col" className="relative px-6 py-3">
                              <span className="sr-only">Action</span>
                            </th>
                          </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                          {subscriptions.map(subscription => {
                            return <tr key={subscription.id}>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="flex items-center">
                                  <div>
                                    <div className="text-sm font-medium text-gray-900">
                                      {subscription.name}
                                    </div>
                                    <div className="text-sm text-gray-500">
                                      {subscription.mobile}
                                    </div>
                                    <div className="text-sm text-gray-500">
                                      {subscription.email}
                                    </div>
                                  </div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="text-sm text-gray-900">{subscription.product.name}</div>
                                <div className="text-sm text-gray-500"><AmountText amount={subscription.product.price} /></div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <AppStatusLabel status={subscription.status} />
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                {subscription.status !== 2 ? <Link to={`/subscription/${subscription.id}`}><p className="text-indigo-600 hover:text-indigo-900">View</p></Link> : false}
                              </td>
                            </tr>
                          })}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </>}
          </div>
        </div>
      </Main>
    )
  }

  componentDidMount() {
    this.getSubscriptions()
  }

  getSubscriptions = async () => {
    AppStore.dispatch({ type: 'LOADING', loading: true })

    const response = await ApiGet('subscriptions');

    if (response.status === 'success') {
      this.setState({ subscriptions: response.data.data })
    } else {
      this.MessageRef.current.show(response.message)
    }

    AppStore.dispatch({ type: 'LOADING', loading: false })
  }
}

const mapStateToProps = (state) => {
  const { loading } = state
  return { loading: loading }
};

const SubscriptionsConnect = connect(mapStateToProps)(props => {
  return <Subscriptions {...props} />
})

export default withRouter(SubscriptionsConnect)