import { Dialog, Transition } from '@headlessui/react'
import { ArrowRightIcon } from '@heroicons/react/outline'
import React, { Fragment, useState } from 'react'
import { useSelector } from 'react-redux'
import { AppRow } from '../AppComponents'
import Button from '../Button'

const ProductInfoBlock = ({ product }) => {
  let [isOpen, setIsOpen] = useState(false)

  function closeModal() {
    setIsOpen(false)
  }

  function openModal() {
    setIsOpen(true)
  }

  return (
    <>
      <div className="bg-orange-200 flex flex-col justify-between rounded-xl p-4">
        <div>
          <h5 className="text-md font-medium text-orange-800 mb-2">{product.name}</h5>
          {/* <p className="text-sm text-orange-800 mb-2">1-4 Members</p> */}
          <ul className="text-orange-800 mb-2 font-medium text-sm list-disc ml-4">
            <li>1-{product.max_members} Members</li>
            <li>{product.description}</li>
          </ul>
        </div>
        {product.mutual_aid ? <>
          <Button attrs={{ onClick: () => openModal() }} className="px-6" size="sm" display="inline-block" title={<React.Fragment>Learn More <ArrowRightIcon width={18} style={{ marginLeft: 5, transform: 'rotate(-45deg)' }} /></React.Fragment>} />
        </> : false}
      </div>
      <Transition appear show={isOpen} as={Fragment}>
        <Dialog
          as="div"
          className="fixed inset-0 z-10 overflow-y-auto"
          onClose={closeModal}
        >
          <Dialog.Overlay className="fixed inset-0 bg-black opacity-30" />
          <div className="min-h-screen px-4 text-center">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <Dialog.Overlay className="fixed inset-0" />
            </Transition.Child>

            {/* This element is to trick the browser into centering the modal contents. */}
            <span
              className="inline-block h-screen align-middle"
              aria-hidden="true"
            >
              &#8203;
            </span>
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <div className="inline-block w-full max-w-md p-6 my-8 overflow-hidden text-left align-middle transition-all transform bg-white shadow-xl rounded-2xl">
                <Dialog.Title
                  as="h3"
                  className="text-lg font-medium leading-6 text-gray-900"
                >
                  {product.name}
                </Dialog.Title>
                <div className="mt-2">
                  <p className="text-sm text-gray-500">
                    {product.description}
                  </p>
                </div>

                <div className="mt-4">
                  <button
                    type="button"
                    className="inline-flex justify-center px-4 py-2 text-sm font-medium text-blue-900 bg-blue-100 border border-transparent rounded-md hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500"
                    onClick={closeModal}
                  >
                    OK
                  </button>
                </div>
              </div>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition>
    </>
  )
}

export default function ProductsList() {
  const products = useSelector(state => state.products)

  return (
    <div>
      <AppRow>
        <div className="flex-1">
          <div className="grid lg:grid-cols-3 grid-cols-2 gap-10">
            {products.map(product => {
              return <ProductInfoBlock key={`${product.id}-${product.product_type}`} product={product} />
            })}
          </div>
        </div>
      </AppRow>
    </div>
  )
}
