import React, { Component, Fragment } from 'react'
import { AppBackButton, AppLabel, AppStatusLabel } from '../../components/AppComponents'
import { AppDataTable } from '../../components/AppDataTable'
import { InputSelect } from '../../components/form/InputSelect'
import { ApiPost } from '../../lib/AppHelper'
import AdminMain from './AdminMain'
import { Menu, Transition } from '@headlessui/react'
import { ChevronDownIcon } from '@heroicons/react/solid'
import { AppStore } from '../../lib/AppStore'

const RESELLER_TYPES = [
   {
      value: '',
      label: 'All'
   },
   {
      value: 1,
      label: 'Individual'
   },
   {
      value: 2,
      label: 'MLM'
   },
   {
      value: 5,
      label: 'MLM Employee'
   },
   {
      value: 3,
      label: 'MFI'
   },
   {
      value: 6,
      label: 'MFI Employee'
   },
   {
      value: 4,
      label: 'Coporate'
   }
];

const ItemAction = ({ item, changeResellerType, changeResellerStatus }) => {

   let options = false

   options = <>
      {item.type === 1 ? <Menu.Item>
         {({ active }) => (
            <button onClick={() => changeResellerType(item.id, 2)}
               className={`${active ? 'bg-violet-500 text-primary' : 'text-gray-900'
                  } group flex rounded-md items-center w-full px-2 py-2 text-sm`}
            >
               Make MLM
            </button>
         )}
      </Menu.Item> : false}
      {item.type === 2 ? <Menu.Item>
         {({ active }) => (
            <button onClick={() => changeResellerType(item.id, 1)}
               className={`${active ? 'bg-violet-500 text-primary' : 'text-gray-900'
                  } group flex rounded-md items-center w-full px-2 py-2 text-sm`}
            >
               Make Individual
            </button>
         )}
      </Menu.Item> : false}
      {item.status === 3 ? <Menu.Item>
         {({ active }) => (
            <button onClick={() => changeResellerStatus(item.id, 1)}
               className={`${active ? 'bg-violet-500 text-primary' : 'text-gray-900'
                  } group flex rounded-md items-center w-full px-2 py-2 text-sm`}
            >
               Approve Account
            </button>
         )}
      </Menu.Item> : false}
      {item.status === 1 ? <Menu.Item>
         {({ active }) => (
            <button onClick={() => changeResellerStatus(item.id, 2)}
               className={`${active ? 'bg-violet-500 text-primary' : 'text-gray-900'
                  } group flex rounded-md items-center w-full px-2 py-2 text-sm`}
            >
               Block Account
            </button>
         )}
      </Menu.Item> : false}
      {item.status === 2 ? <Menu.Item>
         {({ active }) => (
            <button onClick={() => changeResellerStatus(item.id, 1)}
               className={`${active ? 'bg-violet-500 text-primary' : 'text-gray-900'
                  } group flex rounded-md items-center w-full px-2 py-2 text-sm`}
            >
               Unblock Account
            </button>
         )}
      </Menu.Item> : false}
   </>

   return (
      <Menu as="div" className="relative inline-block text-left">
         <div>
            <Menu.Button className="inline-flex justify-center w-full px-4 py-2 text-sm font-medium text-white bg-primary-300 rounded-md hover:bg-opacity-70 focus:outline-none focus-visible:ring-2 focus-visible:ring-white focus-visible:ring-opacity-75">
               Options
               <ChevronDownIcon
                  className="w-5 h-5 ml-2 -mr-1 text-violet-200 hover:text-violet-100"
                  aria-hidden="true"
               />
            </Menu.Button>
         </div>
         <Transition
            as={Fragment}
            enter="transition ease-out duration-100"
            enterFrom="transform opacity-0 scale-95"
            enterTo="transform opacity-100 scale-100"
            leave="transition ease-in duration-75"
            leaveFrom="transform opacity-100 scale-100"
            leaveTo="transform opacity-0 scale-95"
         >
            <Menu.Items className="absolute right-0 w-56 mt-2 origin-top-right bg-white divide-y divide-gray-100 rounded-md shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none z-10">
               <div className="px-1 py-1 ">
                  {options}
               </div>
            </Menu.Items>
         </Transition>
      </Menu>
   )
}


export class AdminResellers extends Component {

   DataTable = React.createRef();
   MessageRef = React.createRef();

   state = { type: "" }

   render() {
      const { type } = this.state;
      return (
         <AdminMain MessageRef={this.MessageRef}>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
            <div className="w-full px-6 flex flex-col justify-between space-y-6">
               <div>
                  <AppDataTable ref={this.DataTable} MessageRef={this.MessageRef} endpoint="resellers" header={['Name', 'Status', 'Mobile', 'Type', 'Actions']} render={(item) => {
                     return <>
                        <td className="px-6 py-4 whitespace-normal max-w-xs">{item.name}</td>
                        <td className="px-6 py-4 whitespace-nowrap">
                           <AppStatusLabel status={item.status} inactive="Inactive" pending="Pending Profile" waiting="Waiting for approval" />
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">{item.mobile}</td>
                        <td className="px-6 py-4 whitespace-nowrap">
                           <AppLabel title={item.type_title} color={item.type_color} />
                        </td>
                        <td>
                           <ItemAction item={item} changeResellerType={(id, type) => this.changeResellerType(id, type)} changeResellerStatus={(id, type) => this.changeResellerStatus(id, type)} />
                        </td>
                     </>
                  }} params={{ type: type }} filters={<>
                     <div className="w-48">
                        <InputSelect label="Reseller Type" attrs={{ value: type, onChange: (e) => this.setState({ type: e.target.value }) }} options={RESELLER_TYPES} />
                     </div>
                  </>} />
               </div>
            </div>
         </AdminMain>
      )
   }

   changeResellerType = async (id, type) => {
      AppStore.dispatch({ type: 'LOADING', loading: true })

      const response = await ApiPost('change-reseller-type', { id: id, type: type });

      if (response.status === 'success') {
         this.MessageRef.current.show(response.data.message, "success");
      } else {
         this.MessageRef.current.show(response.message, 'error')
      }

      AppStore.dispatch({ type: 'LOADING', loading: false })

      this.DataTable.current.getItems();
   }

   changeResellerStatus = async (id, status) => {
      AppStore.dispatch({ type: 'LOADING', loading: true })

      const response = await ApiPost('change-reseller-status', { id: id, status: status });

      if (response.status === 'success') {
         this.MessageRef.current.show(response.data.message, "success");
      } else {
         this.MessageRef.current.show(response.message, 'error')
      }

      AppStore.dispatch({ type: 'LOADING', loading: false })

      this.DataTable.current.getItems();
   }
}

export default AdminResellers
